<template>
  <div class="container">
    <vue-headful
      title="About | Rhys Matthew | Web / UI Developer"
      description="Rhys Matthew is based in Nottingham, UK. Studying Web Development BSc at Manchester Metropolitan University, He has a passion for building digital interfaces and creating interactive websites."
      keywords="About Rhys, About, UI developer, UX Design, Web design, Front-end development, JavaScript dev, Freelance, web engineer, CSS, PHP, Rhys Matthew Web Developer"
      :head="{
        'meta[property=\'og:url\']': {content: 'https://rhys.digital/about/'},
        'meta[name=\'twitter:site\']': {content: 'https://rhys.digital/about/'},
        'link[rel=\'canonical\']': {href: 'https://rhys.digital/about/'}
      }"
    />

    <section class="section pb-0">
      <div class="mw--650">
        <div class="section__header">
          <h1 class="h2">About</h1>
        </div>
        <div class="section__block">
          <prismic-rich-text :field="fields.aboutIntro"/>
        </div>
      </div>
    </section>

    <section class="section pb-0">
      <div class="mw--650">
        <div class="section__header">
          <h2 class="h2">Experience with</h2>
        </div>
        <div class="section__block">
          <ul class="list list--flex">
            <li v-for="(aboutTool, index) in fields.aboutTools" :key="index">
              {{ aboutTool.about_tool }}
            </li>
          </ul>
        </div>
      </div>
    </section>

    <section class="section">
      <div class="mw--650">
        <div class="section__header">
          <h2 class="h2">What I can do for you</h2>
        </div>
        <div class="section__block">
          <ul class="list">
            <li v-for="(aboutService, index) in fields.aboutServices" :key="index">
              {{ aboutService.about_service }}
            </li>
          </ul>
        </div>
      </div>
    </section>

  </div>
</template>
<script>
export default {
  data: function () {
    return {
      fields: {
        aboutIntro: [],
        aboutTools: [],
        aboutServices: []
      }
    }
  },
  mounted () {
    // eslint-disable-next-line
    setTimeout (() => {
      const pageChange = new Event('pageChange')
      document.dispatchEvent(pageChange)
    }, 1500)

    this.getAboutContent()
  },
  methods: {
    getAboutContent: function () {
      this.$prismic.client.getSingle('about')
        .then((response) => {
          this.fields.aboutIntro = response.data.about_intro
          this.fields.aboutTools = response.data.about_tools
          this.fields.aboutServices = response.data.about_services

          setTimeout(() => {
            document.body.classList.remove('loading')
            document.body.classList.add('loaded')
          }, 1000)
        })
    }
  }
}
</script>
